"use client"

import Link from "next/link"
import { useRouter } from "next/navigation"
import { Button } from "@/components/ui/button"
import { Card } from "@/components/ui/card"
import { Avatar, AvatarFallback } from "@/components/ui/avatar"
import { Input } from "@/components/ui/input"
import { Badge } from "@/components/ui/badge"
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs"
import { useEffect, useState, useCallback, useRef } from "react"
import {
  TrendingUp, TrendingDown, Target, Calendar, MessageSquare, Settings,
  Wallet, PiggyBank, CreditCard, LogOut, Lock, Plus, Sparkles,
  RefreshCw, Trash2, Loader2, ShieldCheck, Send, Users, User, Bot, Circle
} from "lucide-react"
import Image from "next/image"

// WebSocket 관련
import Stomp from 'stompjs';
import SockJS from 'sockjs-client';

interface LinkedAccount {
  bankAccount: string; bankName: string; createdAt: string; locked: boolean;
}

interface Message {
  sender: string; content: string; timestamp: string; type: 'TALK' | 'AI' | 'ENTER';
}

export default function DashboardPage() {
  const router = useRouter()
  const [userName, setUserName] = useState("")
  const [isLoading, setIsLoading] = useState(true)
  
  // 상태 관리
  const [linkedAccounts, setLinkedAccounts] = useState<LinkedAccount[]>([])
  const [isLinked, setIsLinked] = useState(false) 
  const [isSynced, setIsSynced] = useState(false)
  const [hasGoals, setHasGoals] = useState(true)

  // 채팅 관련 상태
  const [activeTab, setActiveTab] = useState("AI")
  const [messages, setMessages] = useState<Message[]>([])
  const [inputValue, setInputValue] = useState("")
  const [isConnected, setIsConnected] = useState(false)
  const stompClient = useRef<Stomp.Client | null>(null);
  const scrollRef = useRef<HTMLDivElement>(null);

  // 1. 초기 데이터 로드
  const fetchData = useCallback(async () => {
    const token = localStorage.getItem("accessToken")
    const userId = localStorage.getItem("userId")
    if (!token) { router.push("/login"); return; }

    try {
      const storedName = localStorage.getItem("name")
      if (storedName) setUserName(storedName)

      const response = await fetch("http://34.50.7.8:3000/v1/account/user-account", {
        method: "POST",
        headers: { "Authorization": `Bearer ${token}`, "Content-Type": "application/json" },
        body: JSON.stringify({ userId: userId }) 
      })

      if (response.ok) {
        const data = await response.json()
        if (data.isSuccess && data.result?.length > 0) {
          setLinkedAccounts(data.result); setIsLinked(true); setIsSynced(true);
        }
      }
    } catch (e) { console.error(e) } finally { setIsLoading(false) }
  }, [router])

  // 2. 채팅 연결 로직 (WebSocket)
  const connectChat = useCallback(() => {
    const socket = new SockJS('http://sparta-nextme.xyz:18080/ws-stomp');
    const client = Stomp.over(socket);
    
    const jwt = localStorage.getItem("accessToken");
    
    client.connect({ Authorization: `Bearer ${jwt}` }, (frame) => {
      setIsConnected(true);
      stompClient.current = client;
      
      // 샘플 채널 구독 (실제 구현시 방 ID 동적으로 할당)
      client.subscribe('/sub/chat/room/public', (message) => {
        const newMessage = JSON.parse(message.body);
        setMessages((prev) => [...prev, newMessage]);
      });
    }, (error) => {
      console.error('STOMP Error:', error);
      setIsConnected(false);
    });
  }, []);

  useEffect(() => {
    fetchData();
    connectChat();
    return () => stompClient.current?.disconnect(() => setIsConnected(false));
  }, [fetchData, connectChat]);

  // 메시지 전송
  const sendMessage = () => {
    if (!inputValue.trim() || !stompClient.current) return;

    const chatMessage = {
      type: 'TALK',
      content: inputValue,
      sender: userName || '사용자',
      roomId: activeTab === "GROUP" ? "public" : "private-1"
    };

    stompClient.current.send("/pub/chat/message", {}, JSON.stringify(chatMessage));
    setInputValue("");
  };

  // 자동 스크롤
  useEffect(() => {
    if (scrollRef.current) {
      scrollRef.current.scrollTop = scrollRef.current.scrollHeight;
    }
  }, [messages]);

  const handleLogout = async () => {
    if (confirm("로그아웃 하시겠습니까?")) {
      localStorage.clear();
      router.push("/");
    }
  }

  if (isLoading) return (
    <div className="min-h-screen flex flex-col items-center justify-center bg-background">
      <Loader2 className="h-10 w-10 animate-spin text-primary" />
    </div>
  );

  return (
    <div className="min-h-screen bg-[#F8FAFC] text-slate-900">
      {/* Header */}
      <header className="border-b bg-white/80 backdrop-blur-md sticky top-0 z-50">
        <div className="container mx-auto px-4 h-16 flex items-center justify-between">
          <Link href="/dashboard"><Image src="/logo.png" alt="Logo" width={100} height={28} className="h-7 w-auto" /></Link>
          <div className="flex items-center gap-3">
            <div className="flex items-center gap-1.5 px-3 py-1 bg-slate-100 rounded-full">
              <Circle className={`h-2 w-2 fill-current ${isConnected ? 'text-green-500' : 'text-red-500'}`} />
              <span className="text-[10px] font-bold text-slate-500">{isConnected ? 'LIVE' : 'OFFLINE'}</span>
            </div>
            <Button variant="ghost" size="icon" onClick={handleLogout}><LogOut className="h-5 w-5 text-slate-400" /></Button>
            <Avatar className="h-8 w-8 border-2 border-white shadow-sm">
              <AvatarFallback className="bg-primary text-white text-xs">{userName.charAt(0)}</AvatarFallback>
            </Avatar>
          </div>
        </div>
      </header>

      <main className="container mx-auto px-4 py-8 grid lg:grid-cols-12 gap-6">
        {/* 왼쪽: 대시보드 메인 (8/12) */}
        <div className="lg:col-span-8 space-y-6">
          <section>
            <h1 className="text-2xl font-bold tracking-tight mb-1">안녕하세요, {userName}님</h1>
            <p className="text-slate-500 text-sm">오늘의 금융 요약 및 AI 인사이트입니다.</p>
          </section>

          {/* 자산 요약 카드 */}
          <div className="grid md:grid-cols-3 gap-4">
            <Card className="p-5 border-none shadow-sm bg-white">
              <p className="text-xs font-bold text-slate-400 uppercase mb-1">총 자산</p>
              <p className="text-xl font-black">₩45,280,000</p>
              <div className="flex items-center gap-1 text-green-500 text-[10px] font-bold mt-2">
                <TrendingUp className="h-3 w-3" /> 2.4% 증가
              </div>
            </Card>
            <Card className="p-5 border-none shadow-sm bg-white">
              <p className="text-xs font-bold text-slate-400 uppercase mb-1">월 저축</p>
              <p className="text-xl font-black text-primary">₩1,200,000</p>
              <div className="h-1 w-full bg-slate-100 rounded-full mt-3 overflow-hidden">
                <div className="h-full bg-primary w-[70%]" />
              </div>
            </Card>
            <Card className="p-5 border-none shadow-sm bg-white">
              <p className="text-xs font-bold text-slate-400 uppercase mb-1">월 지출</p>
              <p className="text-xl font-black text-rose-500">₩2,450,000</p>
              <p className="text-[10px] text-slate-400 mt-2">지난달 대비 ₩12,000 절약</p>
            </Card>
          </div>

          {/* 목표 및 AI 분석 (기존 유지) */}
          <Card className="p-6 border-none shadow-sm bg-white relative overflow-hidden">
            <h2 className="font-bold flex items-center gap-2 mb-4"><Sparkles className="h-4 w-4 text-primary" /> AI 재무 리포트</h2>
            <div className="p-4 rounded-xl bg-slate-50 border border-slate-100 italic text-sm text-slate-600">
              "현재 지출 패턴을 유지하면 6개월 내에 주택 자금 목표의 80%에 도달할 수 있습니다."
            </div>
          </Card>

          <Card className="p-6 border-none shadow-sm bg-white">
            <h2 className="font-bold mb-4">내 연동 계좌</h2>
            <div className="space-y-3">
              {linkedAccounts.map((acc, i) => (
                <div key={i} className="flex items-center justify-between p-3 rounded-lg border border-slate-50 bg-slate-50/50">
                  <div className="flex items-center gap-3">
                    <div className="w-8 h-8 rounded-full bg-white border flex items-center justify-center text-[10px] font-bold text-primary">{acc.bankName.slice(0,2)}</div>
                    <div><p className="text-sm font-bold">{acc.bankName}</p><p className="text-[10px] text-slate-400">{acc.bankAccount}</p></div>
                  </div>
                  <Button variant="ghost" size="icon" className="h-8 w-8 text-slate-300"><Trash2 className="h-4 w-4" /></Button>
                </div>
              ))}
            </div>
          </Card>
        </div>

        {/* 오른쪽: 채팅 통합 센터 (4/12) */}
        <div className="lg:col-span-4 h-[calc(100vh-160px)] sticky top-24">
          <Card className="h-full flex flex-col border-none shadow-xl bg-white overflow-hidden">
            <Tabs defaultValue="AI" className="flex flex-col h-full" onValueChange={setActiveTab}>
              <div className="p-4 border-b">
                <TabsList className="grid grid-cols-3 w-full bg-slate-100">
                  <TabsTrigger value="GROUP" className="text-xs gap-1.5"><Users className="h-3.5 w-3.5" /> 그룹</TabsTrigger>
                  <TabsTrigger value="DIRECT" className="text-xs gap-1.5"><User className="h-3.5 w-3.5" /> 1:1</TabsTrigger>
                  <TabsTrigger value="AI" className="text-xs gap-1.5"><Bot className="h-3.5 w-3.5" /> AI</TabsTrigger>
                </TabsList>
              </div>

              <div className="flex-1 overflow-hidden flex flex-col">
                <div ref={scrollRef} className="flex-1 overflow-y-auto p-4 space-y-4 scrollbar-hide">
                  {messages.length === 0 && (
                    <div className="h-full flex flex-col items-center justify-center text-slate-300 space-y-2">
                      <MessageSquare className="h-10 w-10 opacity-20" />
                      <p className="text-xs font-medium">대화를 시작해보세요</p>
                    </div>
                  )}
                  {messages.map((msg, i) => (
                    <div key={i} className={`flex flex-col ${msg.sender === userName ? 'items-end' : 'items-start'}`}>
                      <div className="flex items-center gap-1.5 mb-1">
                        <span className="text-[10px] font-bold text-slate-500">{msg.sender}</span>
                      </div>
                      <div className={`max-w-[85%] px-3 py-2 rounded-2xl text-sm shadow-sm ${
                        msg.sender === userName 
                        ? 'bg-primary text-white rounded-tr-none' 
                        : 'bg-slate-100 text-slate-800 rounded-tl-none'
                      }`}>
                        {msg.content}
                      </div>
                    </div>
                  ))}
                </div>

                <div className="p-4 border-t bg-slate-50/50">
                  <div className="relative">
                    <Input 
                      placeholder={activeTab === "AI" ? "AI에게 재무 질문하기..." : "메시지 입력..."}
                      className="pr-10 bg-white border-slate-200 focus-visible:ring-primary h-11 rounded-xl shadow-inner"
                      value={inputValue}
                      onChange={(e) => setInputValue(e.target.value)}
                      onKeyPress={(e) => e.key === 'Enter' && sendMessage()}
                    />
                    <Button 
                      size="icon" 
                      className="absolute right-1 top-1 h-9 w-9 rounded-lg"
                      onClick={sendMessage}
                    >
                      <Send className="h-4 w-4" />
                    </Button>
                  </div>
                  <p className="text-[9px] text-center text-slate-400 mt-2 font-medium">
                    {activeTab === "DIRECT" ? "전문가가 실시간 확인 중입니다." : "NextMe Chat Service v1.0"}
                  </p>
                </div>
              </div>
            </Tabs>
          </Card>
        </div>
      </main>
    </div>
  )
}